import { createContext, useState } from 'react';

// –û–±—ä—è–≤–∏–ª–∏ –∫–æ–Ω—Ç–µ–∫—Å—Ç
export const MoneyContext = createContext();

export const currencyArr = [
  { name: 'RUB', rate: 1, },
  { name: 'USD', rate: 1/70, },
  { name: 'EUR', rate: 1/80, },
]

// –ü—Ä–æ—Å—Ç–æ —Ñ—É–Ω–∫—Ü–∏—è, —Å —Å–æ—Å—Ç–æ—è–Ω–∏–µ–º, –∫–æ—Ç–æ—Ä–∞—è –ø—Ä–∏–Ω–∏–º–∞–µ—Ç –∫–∞–∫–∏–µ —Ç–æ –ø—Ä–æ–ø—ã
export default function MoneyProvider({ children }) {
  const [currentCurrency, setCurrent–°urrency] = useState('RUB');

  const recalculatePrice = (price) => {
    return currencyArr.filter(currency => currency.name === currentCurrency)
      .map(currency => (price * currency.rate).toFixed(2) + currency.name);
  }
  // –í–æ–∑–≤—Ä–∞—â–∞–µ—Ç –æ–±—ä–µ–∫—Ç (children) –æ–±–µ—Ä–Ω—É—Ç—ã–π –≤ –ø—Ä–æ–≤–∞–π–¥–µ—Ä,
  // –∏ –ø–µ—Ä–µ–¥–∞–µ—Ç —ç—Ç–æ–º—É —á–∏–ª–¥—Ä–µ–Ω—É —Å–æ—Å—Ç–æ—è–Ω–∏–µ —Ñ—É–Ω–∫—Ü–∏–∏ –∏ –º–µ—Ç–æ–¥—ã
  return (
    <MoneyContext.Provider value={{ currentCurrency, setCurrent–°urrency, recalculatePrice }}>
      {children}
    </MoneyContext.Provider>
  )
}
