import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faStar,
} from "@fortawesome/free-solid-svg-icons";
import style from './rate.module.css';
import { useState } from 'react';

export default function Rate({ rating, handleRateChange }) {

  // –ú–µ–Ω—è–µ—Ç—Å—è –¢–û–õ–¨–ö–û –µ—Å–ª–∏ —É –Ω–∞—Å –ø—Ä–∏—Ö–æ–¥–∏—Ç —Ñ—É–Ω–∫—Ü–∏—è, –∫–æ—Ç–æ—Ä–∞—è –º–µ–Ω—è–µ—Ç —Ä–µ–π—Ç–∏–Ω–≥
  const [rate, setRate] = useState(undefined);

  return (
    <div>
      {
        [...Array(5)].map((_, idx) => {
          return <div
            key={idx}
            // –ï—Å–ª–∏ —Ñ—É–Ω–∫—Ü–∏—é –Ω–µ –ø–µ—Ä–µ–¥–∞–ª–∏ –≤ –ø—Ä–æ–ø—ã!
            onClick={handleRateChange ? (() => {
              handleRateChange(idx + 1);
              setRate(idx + 1);
            })
              :
              (undefined)
            }
            className={!rate ?
              (idx < rating ? style.red : style.white)
              :
              (idx < rate ? style.red : style.white)}
          >
            <i><FontAwesomeIcon icon={faStar} /></i>
          </div>
        })
      }
    </div >
  )
}