import requests from '../requests/requests';
import { userByIdSelector } from './users';
import { push } from 'connected-react-router';

import {
  createAction,
  createAsyncThunk,
  createSlice,
  createEntityAdapter
} from '@reduxjs/toolkit';

import {
  STATUS
} from '../constants/constants';

export const ADD_REVIEW = 'ADD_REVIEW';
export const FETCH_REVIEWS = 'FETCH_REVIEWS';

// ------------------------------- Actions -------------------------------

export const addReview = createAction(
  'reviews/add', // –ù–∞–∑–≤–∞–Ω–∏–µ action-a
  (review, props) => ( // –§—É–Ω–∫—Ü–∏—è
    {
      type: ADD_REVIEW, //FIXME –ú–æ–∂–Ω–æ —É–±—Ä–∞—Ç—å!
      payload: { // –î–∞–Ω–Ω—ã–µ
        review,
        restaurantId: props.restaurantId,
      },
      meta: { // –°–ª—É–∂–µ–±–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ
        generateId: ['reviewId', 'userId']
      }
    }
  )
);

export const loadReviews = createAsyncThunk(
  'reviews/load',
  (restaurantId, { dispatch }) => {
    return requests.loadReviews(restaurantId).catch(error => {
      dispatch(push('/error'));
      throw error; //! –û–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –Ω—É–∂–Ω–æ –≤—ã–∫–∏–¥—ã–≤–∞—Ç—å –æ—à–∏–±–∫—É! –ß—Ç–æ–±—ã –ø—Ä–æ–º–∏—Å –∑–∞—Ä–µ–¥–∂–µ–∫—Ç–∏–ª—Å—è
    });
  }
);

// ------------------------------- Reducer -------------------------------
// –≠—Ç–æ —Å—É—â–Ω–æ—Å—Ç—å, –∫–æ—Ç–æ—Ä–∞—è –≤ —Å–µ–±–µ —Å–æ–¥–µ—Ä–∂–∏—Ç –æ–±—ä–µ–∫—Ç —Å –º–µ—Ç–æ–¥–∞–º–∏ –¥–ª—è —Ä–∞–±–æ—Ç—ã —Å –∫–æ–ª–ª–µ–∫—Ü–∏—è–º–∏
const Reviews = createEntityAdapter();

const initialState = {
  ...Reviews.getInitialState(),
  status: STATUS.empty,
  error: null
}

const { reducer } = createSlice({
  name: 'reviews',
  initialState,
  reducers: {
    // An object of "case reducers". Key names will be used to generate actions.
    //reducers: Object<string, ReducerFunction | ReducerAndPrepareObject>

    // –ö–∞–∫ —è –ø–æ–Ω—è–ª, —Ç—É—Ç –º—ã –∏ —Å–≤–æ–∏ —ç–∫—à–µ–Ω—ã —Ñ–æ—Ä–º–∏—Ä—É–µ–º –∏ —Ä–µ–¥—å—é—Å–µ—Ä—ã, 
    // –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥—É—Ç –≤—ã–ø–æ–ª–Ω—è—Ç—å—Å—è –ø—Ä–∏ —ç—Ç–∏—Ö —ç–∫—à–µ–Ω–∞—Ö!
  },
  extraReducers: {
    // –¢.–∫. —ç–∫—à–µ–Ω—ã —É–∂–µ –µ—Å—Ç—å, 
    // –Ω—É–∂–Ω–æ –∫ –Ω–∏–º –ø—Ä–æ—Å—Ç–æ —Ä–µ–¥—å—é—Å–µ—Ä—ã(—Ä–µ–∞–∫—Ü–∏–∏ –Ω–∞ —ç–∫—à–Ω!) –ø—Ä–∏–≤—è–∑–∞—Ç—å!
    [loadReviews.pending.type]: (state, action) => {
      return {
        ...state,
        status: STATUS.loading,
        error: null
      }
    },
    [loadReviews.fulfilled.type]: (state, action) => {
      const { payload } = action;

      const entities = payload.reduce((acc, review) => (
        {
          ...acc,
          [review.id]: review
        }
      ), {});

      state.status = STATUS.loaded;
      Reviews.addMany(state, entities);
    },
    [loadReviews.rejected.type]: (state, action) => {

      const { error } = action;

      return {
        ...state,
        status: STATUS.error,
        error: error
      }
    },
    [addReview.type]: (state, action) => {

      const { review } = action.payload;
      const { reviewId, userId } = action.meta;

      Reviews.addOne(state, {
        id: reviewId,
        userId,
        text: review.text,
        rating: review.rating
      });
    }
  }
});

export default reducer;

// ------------------------------- Selectors -------------------------------
export const reviewsMap = state => state.reviews.entities;

export const reviewByIdSelector = (state, props) => {
  return reviewsMap(state)[props.id];
}

export const reviewByIdWithUserSelector = (state, props) => {
  const review = reviewsMap(state)[props.id];
  const user = userByIdSelector(state, { id: review.userId });

  return {
    id: review.id,
    text: review.text,
    rating: review.rating,
    user: {
      ...user
    }
  }
}